import WaveConsole from './consoles/waveConsole';
import SignalConsole from './consoles/signalConsole';


import NumberStepper from './ui/numberStepper';
import Slider from './ui/slider';
import Button from './ui/button';


interface Props {

	model: any;
	paramsForModel: any;
	inputHandler: Function;

}

const Console = ( props: Props ) => {

	const { model, paramsForModel, inputHandler } = props;

	function selectConsole( CONSOLE_OPTION: string ) {

		switch( CONSOLE_OPTION ) {

			case 'WAVE': 
				return ( <WaveConsole  params={paramsForModel} inputHandler={inputHandler} /> );
				break;
			case 'SIGNAL': 
				return ( <SignalConsole  params={paramsForModel} inputHandler={inputHandler} /> );
				break;
		}
	};

	return (

	    <>		
		
			{ selectConsole( model?.option ) }

	    </>

	)
};

export default Console;
